import { FOR_EXPR, NAME } from "../tokens";
import { parseBlockBody } from "./block-body";
export function parseFor(p) {
  p.expect("@for");
  p.skipSpaces();
  var withIndex;

  if (withIndex = p.peek() === "(") {
    p.advance(1);
  }

  var forIndex = "";
  var forName = p.expect(NAME, "entry name")[0];
  p.skipSpaces();

  if (withIndex) {
    p.expect(",");
    p.skipSpaces();
    forIndex = p.expect(NAME, "index name")[0];
    p.skipSpaces();
    p.expect("\\)");
    p.skipSpaces();
  }

  p.expect("in");
  p.skipSpaces();
  var expr = p.expect(FOR_EXPR, "the data to be iterated")[0];
  p.skipSpaces();
  var node = {
    type: "op-for",
    forName: forName,
    expr: expr,
    localData: [],
    children: [],
    namedChildren: {}
  };

  if (withIndex) {
    node.forIndex = forIndex;
  }

  parseBlockBody(p, node);
  return node;
}