import { parseBlockBody } from "./block-body";
export function parseIf(p) {
  return parseIfBlocks(p, "@if", "op-if", true);
}
export function parseElsif(p) {
  return parseIfBlocks(p, "@elsif", "op-elsif", true);
}
export function parseElse(p) {
  return parseIfBlocks(p, "@else", "op-else", false);
}

function parseIfBlocks(p, kw, type, hasExpr) {
  p.expect(kw);
  p.skipSpaces();
  var node = {
    type: type,
    children: [],
    localData: []
  };

  if (hasExpr) {
    var expr = p.consumeTill("{", false);
    p.skipSpaces();
    node.condition = expr.trim();
  }

  parseBlockBody(p, node);
  return node;
}